
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  background: linear-gradient(180deg, #d1fae5, white);
  backdrop-filter: blur(12px);
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.04);
  padding: 10px 30px;
  z-index: 1000;
  border-bottom: 1px solid #e0f2f1;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.sidebar-logo {
  height: 80px;
  width: 220px;
  margin-top: -10px;
  margin-left: -20px;
}


.menu-btn {
  background: transparent;
  border: none;
  font-size: 20px;
  width: 40px;
  height: 40px;
  color: #059669;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin-left: -10px;
  transition: all 0.2s ease;
}

.menu-btn:hover {
  background: #d1fae5;
  transform: scale(1.05);
}


.search-container {
  flex: 1;
  max-width: 400px;
  margin: 0 20px;
  position: relative;
}

.search-input {
  width: 100%;
  padding: 10px 14px;
  font-size: 14px;
  border-radius: 20px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  transition: 0.2s;
}

.search-input:focus {
  outline: none;
  border-color: #34d399;
  box-shadow: 0 0 0 2px rgba(34, 197, 94, 0.2);
  background-color: #fff;
}

.search-icon {
  position: absolute;
  right: 14px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
  color: #888;
  pointer-events: none;
}


.header-actions {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-right: 10px;
}

.header-actions i {
  font-size: 18px;
  padding: 10px;
  color: #059669;
  border-radius: 50%;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s ease;
}

.header-actions i:hover {
  background: #d1fae5;
  transform: scale(1.1);
}


.cart-link {
  position: relative;
  color: #059669;
  text-decoration: none;
}

.cart-count {
  position: absolute;
  top: -5px;
  right: -10px;
  background-color: #f43f5e;
  color: white;
  font-size: 12px;
  border-radius: 999px;
  padding: 2px 6px;
}

.profile-info {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #065f46;
  text-decoration: none;
}

.profile-info img {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  object-fit: cover;
}


.membership-link {
  text-decoration: none;
}

.membership {
  font-size: 13px;
  background: #e6f0ff;
  color: #059669;
  padding: 6px 12px;
  border-radius: 12px;
  font-weight: 600;
  transition: background 0.3s;
}

.membership:hover {
  background: #d0e3ff;
}


.notifications-dropdown {
  position: absolute;
  top: 80px;
  right: 100px;
  background: white;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  padding: 12px;
  border-radius: 10px;
  width: 220px;
  z-index: 1001;
  animation: fadeIn 0.3s ease;
}

.notifications-dropdown ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.notifications-dropdown li {
  padding: 8px 0;
  font-size: 14px;
  border-bottom: 1px solid #eee;
}

.notifications-dropdown li:last-child {
  border-bottom: none;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}


.language-selector {
  position: relative;
  display: flex;
  flex-direction: column;
}

.language-button {
  display: flex;
  align-items: center;
  gap: 6px;
  cursor: pointer;
  padding: 10px;
  border-radius: 30px;
  font-size: 14px;
  color: #333;
  transition: background 0.3s ease;
}

.language-button:hover {
  background: #d1fae5;
}

.language-dropdown {
  position: absolute;
  top: 50px;
  right: 0;
  background: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  width: 140px;
  z-index: 10;
}

.language-dropdown li {
  padding: 10px;
  font-size: 14px;
  cursor: pointer;
  transition: background 0.2s;
}

.language-dropdown li:hover {
  background: #f0fdf4;
}


body.dark-mode {
  background-color: #121212;
  color: #f0f0f0;
}

body.dark-mode .header {
  background: rgba(30, 30, 30, 0.95);
  border-bottom: 1px solid #333;
}

body.dark-mode .search-input {
  background: #2a2a2a;
  color: #fff;
  border-color: #444;
}

body.dark-mode .search-input:focus {
  border-color: #7ee787;
}

body.dark-mode .header-actions i,
body.dark-mode .language-selector,
body.dark-mode .menu-btn {
  background-color: #1f1f1f;
  color: #eee;
}

body.dark-mode .notifications-dropdown {
  background: #2c2c2c;
  color: #ddd;
}

body.dark-mode .membership {
  background-color: #1f2937;
  color: #86efac;
}
