.footer {
  background: #f4fdf9;
  padding: 40px 20px;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;

  margin-bottom: -20px;
}

.footer-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1200px;
  margin: auto;
  gap: 40px;
}

.footer-left {
  flex: 1;
  min-width: 200px;
}

.footer-left h3 {
  color: #00704A;
  margin-bottom: 10px;
}

.footer-left p {
  margin: 5px 0;
}

.social-icons a {
  margin-right: 10px;
  font-size: 18px;
  color: #00704A;
}

.footer-links {
  display: flex;
  flex: 2;
  justify-content: space-between;
  min-width: 300px;
}

.footer-links h4 {
  margin-bottom: 10px;
  color: #00704A;
}

.footer-links ul {
  list-style: none;
  padding: 0;
}

.footer-links ul li a {
  text-decoration: none;
  color: #333;
  font-size: 14px;
  line-height: 1.8;
}

.footer-links ul li a:hover {
  text-decoration: underline;
}

.footer-apps {
  min-width: 180px;
}

.footer-apps h4 {
  color: #00704A;
  margin-bottom: 10px;
}

.footer-apps img {
  width: 140px;
  margin: 5px 0;
}


@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer-links {
    flex-direction: column;
    gap: 20px;
  }
}
