.cart-container {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding: 24px;
  width: 100%;
  margin: 0 auto;
  background-color: #f0fdf4;
  font-family: 'Segoe UI', sans-serif;
}

.cart-left {
  background-color: #fff;
  border-radius: 10px;
  padding: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
}

.cart-left h2 {
  margin-bottom: 10px;
  font-size: 24px;
  color: #065f46;
}

.cart-left p {
  margin-bottom: 20px;
  color: #555;
}

.cart-table {
  width: 100%;
  border-collapse: collapse;
}

.cart-table th,
.cart-table td {
  padding: 14px;
  text-align: center;
  border-bottom: 1px solid #ddd;
  font-size: 15px;
}

.cart-table th {
  background-color: #d1fae5;
  color: #065f46;
  font-weight: 600;
}

.cart-img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 6px;
  margin-right: 12px;
}

.product-info {
  display: flex;
  align-items: center;
  gap: 10px;
}

.qty {
  margin: 0 10px;
  font-weight: bold;
}

button {
  padding: 6px 12px;
  border: none;
  background-color: #22c55e;
  color: white;
  font-weight: 600;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s ease;
}

button:hover {
  background-color: #16a34a;
}

.cart-right {
  background-color: #fff;
  border-radius: 10px;
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
}

.box {
  padding: 15px;
  background: #fafafa;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.box h4 {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 600;
  color: #065f46;
}

.box label {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 10px;
  font-size: 14px;
  color: #222;
}

.box input[type="checkbox"] {
  transform: scale(1.2);
  margin-right: 6px;
  cursor: pointer;
}

.box textarea {
  width: 100%;
  margin-top: 10px;
  padding: 12px;
  font-size: 14px;
  border-radius: 6px;
  border: 1px solid #ccc;
  resize: vertical;
  min-height: 80px;
}

input,
select {
  width: 100%;
  padding: 10px 12px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
}

.summary-box p {
  display: flex;
  justify-content: space-between;
  margin: 6px 0;
  font-size: 15px;
}

.summary-box .grand-total {
  border-top: 1px solid #ccc;
  margin-top: 10px;
  padding-top: 10px;
  font-size: 18px;
  font-weight: bold;
}

.summary-box button {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  margin-top: 16px;
  background-color: #e11d48;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.summary-box button:hover {
  background-color: #be123c;
}

/* Suggested products under cart */
.suggested-products-cart {
  margin-top: 40px;
}

.suggested-products-cart h3 {
  margin-bottom: 16px;
  font-size: 20px;
  color: #065f46;
}

.suggested-scroll {
  display: flex;
  gap: 16px;
  overflow-x: auto;
  padding: 10px 0;
}

.suggested-scroll::-webkit-scrollbar {
  display: none;
}

.suggested-card {
  width: 200px;
  background: white;
  border-radius: 10px;
  padding: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  text-align: center;
}

.suggested-card img {
  width: 100px;
  height: 100px;
  object-fit: contain;
  margin-bottom: 8px;
}

.add-btn-small,
.quantity-controls-small button {
  background-color: #22c55e;
  color: white;
  border: none;
  padding: 6px 10px;
  border-radius: 6px;
  cursor: pointer;
}

.quantity-controls-small {
  display: flex;
  gap: 6px;
  justify-content: center;
  align-items: center;
  margin-top: 6px;
}

.quantity-controls-small span {
  font-weight: bold;
}


@media screen and (max-width: 768px) {
  .cart-container {
    padding: 16px;
  }

  .cart-img {
    width: 45px;
    height: 45px;
  }

  button {
    padding: 6px 10px;
    font-size: 14px;
  }

  .summary-box button {
    font-size: 14px;
  }

  .cart-table th,
  .cart-table td {
    font-size: 13px;
    padding: 10px;
  }
}
