.orders-page {
  padding: 30px;
  background: #f0fdf4;
  min-height: 100vh;
  font-family: 'Segoe UI', sans-serif;
}

.orders-page h2 {
  color: #047857;
  margin-bottom: 24px;
  font-size: 26px;
  font-weight: bold;
}

.orders-list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.order-card {
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.06);
  padding: 24px;
  transition: transform 0.2s;
}

.order-card:hover {
  transform: translateY(-3px);
}

.order-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #065f46;
  margin-bottom: 16px;
}

.order-header h4 {
  font-size: 18px;
  font-weight: 600;
}

.order-items {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-bottom: 16px;
}

.order-item {
  display: flex;
  align-items: center;
  gap: 14px;
  background: #f9fafb;
  padding: 12px 14px;
  border-radius: 10px;
  flex: 1 1 250px;
  border: 1px solid #e5e7eb;
}

.order-item img {
  width: 60px;
  height: 60px;
  object-fit: contain;
  border-radius: 8px;
  background: #fff;
  border: 1px solid #ddd;
}

.order-item p {
  margin: 2px 0;
  font-size: 14px;
}

/* Reorder button */
.reorder-btn {
  background: #10b981;
  color: white;
  padding: 10px 18px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s ease;
}

.reorder-btn:hover {
  background: #059669;
}

/* Invoice + Cancel Button */
.action-buttons {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.action-buttons button {
  padding: 8px 14px;
  font-size: 13px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
}

.invoice-btn {
  background-color: #2563eb;
  color: white;
}

.invoice-btn:hover {
  background-color: #1e40af;
}

.cancel-btn {
  background-color: #f87171;
  color: white;
}

.cancel-btn:hover {
  background-color: #dc2626;
}

/* Order status badge */
.status {
  padding: 4px 10px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  display: inline-block;
  text-transform: capitalize;
  margin-top: 5px;
}

.status.processing {
  background-color: #fef3c7;
  color: #92400e;
  border: 1px solid #fde68a;
}

.status.shipped {
  background-color: #dbeafe;
  color: #1d4ed8;
  border: 1px solid #93c5fd;
}

.status.delivered {
  background-color: #dcfce7;
  color: #065f46;
  border: 1px solid #bbf7d0;
}

.status.cancelled {
  background-color: #fee2e2;
  color: #b91c1c;
  border: 1px solid #fecaca;
}

.status.unknown {
  background-color: #e5e7eb;
  color: #374151;
  border: 1px solid #d1d5db;
}
