* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Inter', sans-serif;
  background: white;
  padding: 20px;
}

.profile-container {
  width: 96%;
  margin: auto;
  background: white;
  border-radius: 15px;
  overflow: hidden;
  padding: 20px;
}

.profile-card {
  padding: 25px;
}

.profile-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: -40px;
}

.profile-img {
  border-radius: 50%;
  width: 90px;
  height: 90px;
  object-fit: cover;
  border: 4px solid white;
  box-shadow: 0 0 5px green;
}

.profile-info {
  flex: 1;
  margin-left: 20px;
}

.profile-info h3 {
  font-size: 20px;
  color: #006400;
}

.profile-info p {
  color: #333;
  font-size: 14px;
}

.edit-btn {
  background-color: #2ecc71;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: background 0.3s;
}

.edit-btn:hover {
  background-color: #27ae60;
}

.profile-form {
  margin-top: 30px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 6px;
  font-weight: 600;
  color: #006400;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 12px 15px;
  border: 1px solid #d1fae5;
  border-radius: 8px;
  background-color: #f0fff4;
  font-size: 14px;
  color: #064e3b;
}

.form-group input:disabled,
.form-group select:disabled {
  background-color: #e6f4ea;
  color: #555;
}

.email-section {
  margin-top: 30px;
}

.email-section h4 {
  margin-bottom: 10px;
  color: #006400;
}

.email-info {
  display: flex;
  align-items: center;
  gap: 15px;
  background: #e9f7ef;
  padding: 12px 16px;
  border-radius: 10px;
  margin-bottom: 10px;
  border: 1px solid #a7f3d0;
}

.email-icon {
  background-color: #d1fae5;
  color: #059669;
  font-size: 20px;
  padding: 8px;
  border-radius: 50%;
}

.email-info p {
  margin: 0;
  font-weight: 500;
  color: #064e3b;
}

.email-info small {
  color: #4b5563;
  font-size: 13px;
}

.add-email-btn {
  background-color: #d1fae5;
  color: #065f46;
  border: none;
  padding: 10px 20px;
  margin-top: 10px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: background 0.3s;
}

.add-email-btn:hover {
  background-color: #a7f3d0;
}

.email-input-box {
  margin-top: 10px;
  display: flex;
  gap: 10px;
}

.email-input-box input {
  flex: 1;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  background-color: #f0fff4;
}

.email-input-box button {
  background-color: #10b981;
  color: white;
  border: none;
  padding: 10px 14px;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.3s;
}

.email-input-box button:hover {
  background-color: #059669;
}


