.membership-page {
  padding: 30px;
  text-align: center;
  background-color: #f9fefb;
  min-height: 100vh;
}

.membership-page h2 {
  color: #065f46;
  margin-bottom: 24px;
  font-size: 28px;
}

.plans-container {
  display: flex;
  justify-content: center;
  gap: 24px;
  flex-wrap: wrap;
}

.plan-card {
  display: flex;
  flex-direction: column; 
  justify-content: space-between; 
  border: 2px solid #ccc;
  border-radius: 16px;
  padding: 20px;
  width: 220px;
  min-height: 320px; 
  background: #ffffff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s, border-color 0.3s;
}

.plan-card:hover {
  transform: translateY(-4px);
  border-color: #10B981;
}

.plan-card.selected {
  border-color: #10B981;
  background-color: #D1FAE5;
  transform: scale(1.05);
}

.plan-card h3 {
  margin-bottom: 8px;
  color: #065f46;
  font-size: 20px;
}

.plan-card p {
  font-weight: bold;
  color: #10B981;
  margin-bottom: 12px;
}

.plan-card ul {
  list-style: none;
  padding: 0;
  margin-bottom: 16px;
}

.plan-card li {
  padding: 4px 0;
  font-size: 14px;
  color: #374151;
}

.plan-card button {
  background-color: #10B981;
  color: white;
  border: none;
  padding: 8px 14px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.2s;
}

.plan-card button:hover {
  background-color: #059669;
}

.payment-section {
  margin-top: 40px;
  background: #ecfdf5;
  padding: 20px;
  border-radius: 12px;
  display: inline-block;
  border: 1px solid #a7f3d0;
}

.payment-section h4 {
  color: #047857;
  margin-bottom: 12px;
  font-size: 18px;
}

.pay-btn {
  background: #10B981;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.2s;
}

.pay-btn:hover {
  background: #047857;
}
