.flash-deals {
  margin: 2rem 1rem;
  padding: 1rem;
  background-color: transparent;
  border-radius: 8px;
}

.flash-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flash-products {
  display: flex;
  overflow-x: auto;
  gap: 1rem;
  margin-top: 1rem;
  padding-bottom: 10px;
}
.flash-products::-webkit-scrollbar {
  display: none; 
}

.flash-card {
  width: 220px;
  background: white;
  border: 1px solid #eee;
  border-radius: 8px;
  padding: 10px;
  position: relative;
  flex: 0 0 auto;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
}

.image-wrapper {
  position: relative;
}

.product-image {
  width: 100%;
  height: 150px;
  object-fit: contain;
}

.discount-badge {
  position: absolute;
  top: 8px;
  left: 8px;
  background-color: red;
  color: white;
  padding: 2px 6px;
  font-size: 12px;
  border-radius: 4px;
}

.add-btn-overlay,
.quantity-controls-overlay {
  position: absolute;
  bottom: 10px;
  left: 70%;
  transform: translateX(-50%);
}

.add-btn-overlay {
  background: #2e7d32;
  color: white;
  border: none;
  padding: 5px 12px;
  border-radius: 4px;
  cursor: pointer;
}

.quantity-controls-overlay {
  display: flex;
  align-items: center;
  gap: 6px;
}

.price {
  font-weight: bold;
  margin-top: 8px;
}

.original-price {
  text-decoration: line-through;
  color: gray;
  margin-left: 6px;
  font-size: 0.9rem;
}
