.filter-sort-panel {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 2rem;
  padding: 20px;
  background-color: #ecfdf5;
  border-radius: 12px;
  margin: 1rem 0;
}


.filters-sidebar h4 {
  margin: 0 0 -15px;
  font-size: 1.1rem;
  color: #047857;
}

.filters-sidebar label {
  margin-right: 0.5rem;
  font-weight: 500;
    color: #065f46;

}

.filters-sidebar select {
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  background-color: white;
  min-width: 150px;
}


.sort-options label {
  margin-right: 0.5rem;
  font-weight: 500;
    color: #065f46;

}

.sort-options select {
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
    color: #065f46;
  font-weight: bold;

  background-color: white;
  min-width: 160px;
}
