.carousel {
  position: relative;
  width: 100%;
  height: 320px;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 12px;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}


.carousel-image {
  width: 100%;
  height: 100%;
  object-fit: cover;     
  object-position: center;
  border-radius: 12px;
}



.carousel-indicators {
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 8px;
  z-index: 10;
}

.indicator {
  width: 10px;
  height: 10px;
  background-color: #fff;
  border-radius: 50%;
  opacity: 0.5;
  cursor: pointer;
  transition: 0.3s;
}

.indicator.active {
  opacity: 1;
  transform: scale(1.2);
}
