.sidenav {
  height: 100vh;
  width: 220px;
  background-color: #f0fdf4;
  border-right: 1px solid #bbf7d0;
  padding-top: 24px;
  position: fixed;
  top: 0;
  left: 0;
  transition: width 0.3s ease;
  overflow-x: hidden;
  z-index: 1000;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.05);
}

.sidenav.closed {
  width: 70px;
}

.sidenav-logo {
  font-size: 22px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 24px;
  color: #059669;
  white-space: nowrap;
  padding: 0 16px;
}

.sidenav-links {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 0 10px;
}

.sidenav-link {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 10px 16px;
  border-radius: 40px;
  color: #065f46;
  text-decoration: none;
  font-size: 15px;
  font-weight: 500;
  transition: background-color 0.2s ease, color 0.2s ease;
}

.sidenav-link .icon {
  font-size: 20px;
}

.sidenav-link:hover {
  background-color: #d1fae5;
  color: #047857;
}

.sidenav-link.active {
  background-color: #10b981;
  color: white;
  font-weight: 600;
}

.sidenav.closed .sidenav-link .label {
  display: none;
}

.sidenav.closed .sidenav-link {
  justify-content: center;
  padding: 10px;
}

.sidenav::-webkit-scrollbar {
  display: none;
}
