.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.app::-webkit-scrollbar {
  display: none; 
}
.main-content {
  transition: margin-left 0.3s ease;
  margin-top: 60px; 
}
.main-content::-webkit-scrollbar {
  display: none; 
}
.sidebar-open {
  margin-left: 200px;
}

.sidebar-closed {
  margin-left: 50px;
}

